<template>
  <div class="main-wrap">
    <div class="main-visual">
      <h2><mdicon name="notebook" /></h2>
      <loading v-if="$store.state.loading"></loading>
      <ul v-else>
        <fetch-posts
          v-for="item in items"
          :key="item._id"
          :item="item"
          @refresh="getData"
        ></fetch-posts>
      </ul>
    </div>

    <button>
      <router-link to="/add"><mdicon name="pencil-plus" /></router-link>
    </button>
  </div>
</template>

<script>
import { fetchLists } from "@/api/posts";
import Loading from "@/components/common/Loading";
import FetchPosts from "../components/FetchPosts.vue";
export default {
  components: { FetchPosts, Loading },
  data() {
    return {
      items: [],
    };
  },
  created() {
    this.getData();
  },
  methods: {
    async getData() {
      this.$store.state.loading = true;
      const { data } = await fetchLists();
      this.$store.state.loading = false;
      this.items = data.posts;
    },
  },
};
</script>

<style scoped lang="scss">
.main-wrap {
  height: 100vh;
  background: #fff;
  position: relative;
  overflow-y: scroll;
  .main-visual {
    padding: 20px;
  }
}
button {
  position: absolute;
  right: 30px;
  bottom: 50px;
  background: #ffd740;
  border-radius: 50px;
  width: 50px;
  height: 50px;
  a {
    color: #000;
  }
}
</style>
