<template>
  <li>
    <div class="title">
      <p>제목: {{ item.title }}</p>
    </div>
    <div class="content">
      <p>내용: {{ item.contents }}</p>
      <p>{{ item.createdAt | formatDate }}</p>
    </div>
    <div class="btns">
      <button>
        <a href="javascript:;" @click="remove"
          ><mdicon name="trash-can-outline"
        /></a>
      </button>
      <button>
        <a href="javascript:;" @click="edit"><mdicon name="Pencil" /></a>
      </button>
    </div>
  </li>
</template>

<script>
import { removeItem } from "@/api/posts";
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  methods: {
    async remove() {
      try {
        if (confirm("삭제하시겠어요?")) {
          await removeItem(this.item._id);
          this.$emit("refresh");
        }
      } catch (error) {
        console.log(error);
      }
    },
    edit() {
      this.$router.push(`edit/${this.item._id}`);
    },
  },
};
</script>

<style scoped lang="scss">
li {
  background: #c5c5c5;
  margin: 10px;
  color: #fff;
  position: relative;
  padding: 10px;
  div {
    padding: 5px;
  }
  .title {
  }
  .content {
    display: flex;
    justify-content: space-between;
  }
  .btns {
    position: absolute;
    right: 0px;
    top: 0;
    padding: 10px;
    a {
      color: #333;
    }
  }
}
</style>
